import { mapState } from 'vuex'; 
import postCard from '@/components/postCard';
export default {
	components: {
		postCard
	},
	data() {
		return {
			info: {
				objectId: '',	// 用户Id
				nickName: '未登录',	// 用户昵称
				avatarUrl: '/static/logo/no-login.png',	// 头像
				gender: 1,	// 性别 1-男
				autograph: '签名',	// 签名
				follows: 0,	// 关注数
				fans: 0,	// 粉丝数
				praise: 0,	// 赞数
			},
			pageSetting: {
				pageIndex: 1,	// 页码
				pageSize: 10,	// 每页页数
				totalPage: 1,	// 总页数
				totalSize: 0,	// 总条数数
			},
			postList: [],	// 帖子列表
		}
	},
	computed: {
		...mapState(['hasLogin','userInfo'])
	},
	async onLoad() {
		let that = this;
		uni.getStorage({
		    key: 'userInfo',
		    success: function (res) {
				that.info.objectId = res.data.objectId;
		    }
		});
	},
	onReady(){
		this.getUserInfo();
		this.getPostList();
	},
	// 分享
	onShareAppMessage() {
		return {
			title: '事事通',
			path: '/pages/index/index'
		}
	},
	// 到底
	onReachBottom(){
		if (this.pageSetting.pageIndex <= this.pageSetting.totalPage) {
			//设置列表数据
			this.getPostList()
		}
	},
	methods: {
		// 跳转
		navTo(url){
			uni.navigateTo({
				url: url + '?objectId=' + this.info.objectId
			})
		},
		// 详情、结果
		navToDetails(id){
			uni.navigateTo({
				url: '/pages/post/postDetail?postId=' + id
			})
		},
		// 获取用户信息
		getUserInfo () {
			let that = this;
			uni.showLoading({
				title: '加载中'
			});
			const query = that.Bmob.Query('_User');
			query.get(that.userInfo.objectId).then(res => {
				that.info = res;
			    console.log(res)
				uni.hideLoading();
			}).catch(err => {
			  console.log(err)
			})
		},
		// 获取帖子列表
		getPostList() {
			let that = this;
			uni.showLoading({
				title: '加载中'
			});
			var query = that.Bmob.Query('postList');
			query.limit(10);	// 每页条数
			query.skip(10 * (that.pageSetting.pageIndex - 1));	// 分页查询// 对score字段降序排列
			query.order("-createdAt");
			query.include("author", "_user");
			query.equalTo("author", "==", that.info.objectId);
			query.count().then(res => {
				if(res.count === 0){
					that.postList = []
				}else{
					that.pageSetting.totalPage = parseInt(res.count/that.pageSetting.pageSize) + 1
					query.find().then(res => {
						that.pageSetting.pageIndex += 1;
						that.postList = that.postList.concat(res);
						uni.hideLoading();
					});
				}
			});
		},
	}
}
